<template>
  <el-dialog
    :title="withdraw.id ? '修改' : '新增'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    @close="resetForm('withdraw')"
  >
    <el-form
      ref="withdraw"
      :model="withdraw"
      :rules="withdrawRule"
      label-width="95px"
    >
      <el-form-item prop="name" label="名称 :">
        <el-input size="mini" class="withdrawAddOrUpdate__input" v-model="withdraw.name"></el-input>
      </el-form-item>
      <el-form-item prop="type" label="地址分类 :">
        <el-radio-group v-model="withdraw.type">
          <el-radio :label="1">支付宝</el-radio>
          <el-radio :label="2">银行卡</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item prop="address" label="地址 :">
        <el-input size="mini" class="withdrawAddOrUpdate__input" v-model="withdraw.address"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="withdrawSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { withdrawAddOrUpdate } from "@/api/withdraw.js";

export default {
  name: "withdrawAddOrUpdate",
  data() {
    return {
      visible: false,
      withdraw: {
        id: 0,
        name: "",
        type: 1,
        address: ""
      },
      withdrawRule: {
        name: [{ required: true, message: "名称不能为空", trigger: "blur" }],
        address: [
          { required: true, message: "地址不能为空", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.withdraw.id = 0;
    },
    init(row) {
      this.visible = true;
      if (row && row.id > 0) {
        this.$nextTick(() => {
          Object.keys(this.withdraw).forEach(k => {
            this.withdraw[k] = row[k];
          });
        });
      }
    },
    withdrawSubmit() {
      this.$refs["withdraw"].validate(valid => {
        if (valid) {
          withdrawAddOrUpdate(this.withdraw).then(
            ({ data: { message = "操作成功" } }) => {
              this.$message({
                message,
                type: "success"
              });
              this.$emit("refreshWithdrawList");
              this.visible = false;
            }
          );
        }
      });
    }
  }
};
</script>

<style lang="scss">
$prefix: withdrawAddOrUpdate;

.#{$prefix} {
  &__input {
    width: 350px;
  }
}
</style>
