<template>
  <div class="withdraw">
    <el-form :inline="true">
      <el-row>
        <el-button
          class="withdraw__btn--add"
          type="primary"
          size="mini"
          @click="addOrUpdateHandle()"
          >新增</el-button
        >
      </el-row>
    </el-form>
    <el-table :data="withdrawList" style="width: 100%" border stripe height="700">
      <el-table-column
        prop="id"
        label="ID"
        width="60"
        align="center"
      ></el-table-column>
      <el-table-column prop="type" label="类型" width="100" align="center">
        <template slot-scope="{ row }">
          {{ row.type | changeType }}
        </template>
      </el-table-column>
      <el-table-column prop="name" label="名称" width="240" align="left"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column prop="created_at" label="创建时间" width="200" align="center"></el-table-column>
      <el-table-column prop="address" label="操作" width="180" align="center">
        <template slot-scope="{ row }">
          <el-button-group>
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="addOrUpdateHandle(row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleteHandle(row)"
              >删除</el-button
            >
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>

    <add-or-update
      ref="addOrUpdate"
      v-if="addOrUpdateVisible"
      @refreshWithdrawList="getWithdrawList"
    />
  </div>
</template>
<script>
import AddOrUpdate from "./withdrawAddOrUpdate";

import { withdrawList, deleteWithdraw } from "@/api/withdraw.js";

export default {
  name: "withdraw",
  components: {
    AddOrUpdate
  },
  data() {
    return {
      withdrawList: [],
      addOrUpdateVisible: false
    };
  },
  filters: {
    changeType(val) {
      return val === 1 ? "支付宝" : "银行卡";
    }
  },
  methods: {
    deleteHandle({ id }) {
      this.$confirm(`确定删除该数据?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteWithdraw(id)
            .then(({ data: { message = "删除成功" } }) => {
              this.$message({
                message,
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.getWithdrawList();
                }
              });
            })
            .catch(() => {
              this.$message.error("删除失败！");
            });
        })
        .catch(() => {});
    },
    addOrUpdateHandle(row) {
      this.addOrUpdateVisible = true;
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(row);
      });
    },
    getWithdrawList() {
      withdrawList().then(({ data }) => {
        this.withdrawList = data;
      });
    }
  },
  created() {
    this.getWithdrawList();
  }
};
</script>

<style lang="scss">
$prefix: "withdraw";

.#{$prefix} {
  &__btn--add {
    margin-bottom: 20px;
  }
}
</style>
